"use client"

import type React from "react"
import { createContext, useContext, useState, useEffect } from "react"
import { useRouter, usePathname } from "next/navigation"
import Cookies from "js-cookie"
import { type User, users } from "@/lib/mock-data"


type AuthContextType = {
  user: User | null
  login: (email: string) => Promise<void>
  logout: () => void
  isLoading: boolean
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const router = useRouter()
  const pathname = usePathname()

  useEffect(() => {
    // Simulate checking for existing session (use cookies instead of localStorage)
    const storedUser = Cookies.get("ims_user")
    if (storedUser) {
      try {
        setUser(JSON.parse(storedUser))
      } catch {
        Cookies.remove("ims_user")
      }
    }
    setIsLoading(false)
  }, [])

  const login = async (email: string) => {
    setIsLoading(true)
    // Simulate API call
    await new Promise((resolve) => setTimeout(resolve, 1000))

    const foundUser = users.find((u) => u.email === email)
    if (foundUser) {
      setUser(foundUser)

      // Set cookie, expires in 7 days. Use secure flag in production.
      Cookies.set("ims_user", JSON.stringify(foundUser), {
        expires: 7,
        secure: process.env.NODE_ENV === "production",
        sameSite: "strict",
      })

      // Redirect based on role
      if (
        foundUser.role === "biometricVerifierExaminer" ||
        foundUser.role === "documentVerifierExaminer"
      ) {
        router.push("/verification")
      } else if (
        foundUser.role === "preInterviewExaminer" ||
        foundUser.role === "panelMember"
      ) {
        router.push("/candidates")
      } else {
        // System administrator and any other roles
        router.push("/")
      }
    } else {
      alert(
        "Invalid email. Try systemadmin@system.com, biometric.verifier@system.com, document.verifier@system.com, pre.interview@system.com or panel.member@system.com",
      )
    }
    setIsLoading(false)
  }

  const logout = () => {
    setUser(null)
    Cookies.remove("ims_user")
    router.push("/login")
  }

  return <AuthContext.Provider value={{ user, login, logout, isLoading }}>{children}</AuthContext.Provider>
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (context === undefined) {
    throw new Error("useAuth must be used within an AuthProvider")
  }
  return context
}
